import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ntech_core_config_values } from 'src/app/ntech-models/ntech_core_config_values';
import { NtechAddressData } from 'src/app/ntech-models/ntech_address_data';
import { NtechLead } from 'src/app/ntech-models/ntech_lead';
import { NtechConsumerData } from 'src/app/ntech-models/ntech_consumer_data';
import { NtechProduct } from 'src/app/ntech-models/ntech_products';
import { SharedDataService } from 'src/app/ntech-services/ntech-core-services/shared-data.service';
import { Router } from '@angular/router';
import { NtechAdminService } from 'src/app/ntech-services/ntech-admin.service';
import { NtechContractService } from 'src/app/ntech-services/ntech-contract.service';
import { NtechPagerefresh } from 'src/app/ntech-services/ntech-core-services/ntech-pagerefresh';
import { select, Store } from '@ngrx/store';
import { NtechCommonDataStat } from 'src/app/ntech-storage/ntech-commondata.state';
import { NtechCommonDataModel } from 'src/app/ntech-storage/ntech-commondata.model';

@Component({
    selector: 'ntech-app-crlead',
    templateUrl: './ntech-app-createlead.component.html'
})
export class NtechAppCreateLeadComponent implements OnInit {
    responseData: any;
    corevalues: ntech_core_config_values[];
    ntaddress = new NtechAddressData
    ntlead = new NtechLead;
    ntcons = new NtechConsumerData
    lstntechproduct: NtechProduct[];
    lstmainntechproduct: NtechProduct[];
    lstsubntechproduct: NtechProduct[];
    ntechcommonmode: NtechCommonDataModel;
    changecode: string;
    constructor(private store: Store<NtechCommonDataStat>, private modalService: NgbModal, private contractservice: NtechContractService, private adminservice: NtechAdminService, private shareDate: SharedDataService, private router: Router, private pagerefresh: NtechPagerefresh) {
        this.lstmainntechproduct = []
        this.lstsubntechproduct = []

    }
    ngOnInit(): void {
        this.getProductlist();
        this.generateSharedData();
    }

    saveLead() {
        this.ntaddress.ntAddType = 'P';
        this.ntlead.ntLdHoldBy = this.ntechcommonmode.ntUserId
        this.contractservice.saveLead({
            'ntech_lead': this.ntlead
            , 'ntech_addr': this.ntaddress, 'ntech_cons': this.ntcons
        }).subscribe((data) => {
            if (data) {
                this.responseData = data
                if (this.responseData.responseStatus == true) {
                    alert('Lead Created Successfully.. Your Application in Inbox page redirected automatically');
                    this.router.navigate(['/ntechapp/ntechappdash/ntechdashboard']);
                }
            }

        });
    }

    leadProcesscheck() {
        if (this.ntechcommonmode.leadno)
            this.getLeadData(this.ntechcommonmode.leadno);
    }

    getProductlist() {
        this.adminservice.getProductdata({ 'type': 'ALL' }).subscribe((data) => {
            if (data) {
                this.responseData = data
                if (this.responseData.responseStatus == true) {
                    this.lstntechproduct = this.responseData.paramMap.product;

                    this.lstntechproduct.forEach((args) => {
                        if (args.ntPrdType == '1')
                            this.lstmainntechproduct.push(args)
                    })
                }
            }
            this.leadProcesscheck();
        });
    }

    getValuebyId(ntLdProd: string) {
        this.lstsubntechproduct = [];
        this.lstntechproduct.forEach((args) => {
            if (args.ntPrdLink == ntLdProd)
                this.lstsubntechproduct.push(args)
        })
    }

    getValuebyIndus(args: string, args2: string) {
        if (args2 == 'F')
            this.changecode = args + '@' + this.ntlead.ntLdIndus;
        else
            this.changecode = args + '@' + args2;
    }

    getLeadData(args: string) {
        this.contractservice.getLead({ 'leadno': args }).subscribe((data) => {
            if (data) {
                this.responseData = data
                if (this.responseData.responseStatus == true) {
                    this.getValuebyIndus('N0010', this.responseData.paramMap.ntlead.ntLdIndus);
                    this.getValuebyId(this.responseData.paramMap.ntlead.ntLdProd);
                    this.ntcons = this.responseData.paramMap.ntcons;
                    this.ntaddress = this.responseData.paramMap.ntaddr;
                    this.ntlead = this.responseData.paramMap.ntlead;
                }
            }

        });
    }

    process() {
        this.pagerefresh.refreshPage();
    }

    generateSharedData() {
        this.store.pipe(select('ntechcommondata')).subscribe((args) => {
            this.ntechcommonmode = args;
            if (!this.ntechcommonmode.ntUserId || this.ntechcommonmode.ntUserId == '')
                this.router.navigate(['/ntecherror']);
        }
        );

    }
}