import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NtechMailBox } from 'src/app/ntech-models/ntech_mail_box';
import { NtechMailModel } from 'src/app/ntech-models/ntech_mail.model';
import { SharedDataService } from 'src/app/ntech-services/ntech-core-services/shared-data.service';
import { NtechSharedModel } from 'src/app/ntech-models/ntech-shared.model';
import { share } from 'rxjs/operators';
import { Router } from '@angular/router';
import { NtechAdminService } from 'src/app/ntech-services/ntech-admin.service';
import { NtechContractService } from 'src/app/ntech-services/ntech-contract.service';

@Component({
    selector : 'ntech-app-inbox',
    templateUrl : './ntech-app-inbox.component.html'
})
export class NtechAppInboxComponent implements OnInit
{
       responseData : any;
       ntechmail : NtechMailBox[]
       ntechmailmod : NtechMailModel[]
       sharemodel : NtechSharedModel

    constructor(private modalService: NgbModal,private contractservice : NtechContractService,private sharedata: SharedDataService,private router : Router ){
        this.ntechmail=[]
        this.ntechmailmod=[]
    }
    ngOnInit(): void {
        this.getLeadinbox();
       
        this.sharedata.getData.subscribe((data)=>{
            this.sharemodel=data;
        });
    }

    getLeadinbox()
    {
        this.contractservice.getLeadInbox({}).subscribe((data)=>{
            if(data){
                this.responseData=data
                if(this.responseData.responseStatus==true){
                    for(let i of this.responseData.paramMap.leadmail)
                            this.ntechmailmod.push(i);
                }
            }
    
        });
    }

    gotoLead(args : string){
        this.sharemodel.leadno=args.replace(/['"]+/g, '');
        this.router.navigate(['/ntechapp/ntechappcontract/ntcrlead']);  
    }
}